/* UI elements "Buttons" */
.button {
    position: relative;
    display: block;
    border: none;
    border-radius: 999px;
    padding: 1.25em 2em 1.25em 2em;
    background-color: #787d46;
    text-align: center;
    transition: all 0.5s ease 0s;
    color: #ffffff;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    text-decoration: none;
    overflow: hidden;
    cursor: pointer;
}

.button::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 100%;
    transform: translateY(-50%);
    width: 100%;
    height: 100%;
    background-image: url("../images/bg/bg-button.svg");
    background-position: center;
    background-repeat: repeat-x;
    background-size: auto 90%;
    transition: inherit;
}

.button span {
    position: relative;
}

.button:hover,
.button:focus-within {
    background-color: #676d23;
}

.button:hover::before,
.button:focus-within::before {
    right: 0;
}

@media only screen and (min-width: 768px) {
    .button {
        font-size: 16px;
        line-height: 20px;
    }
}

.button_fw-700 {
    font-weight: 700;
}

.button-group {
    position: relative;
    display: flex;
    overflow: hidden;
    align-items: center;
    border-radius: 999px;
}

.button-group__button_prev,
.button-group__button_next {
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    width: 44px;
    height: 44px;
    background-color: transparent;
    transition: background-color 0.25s ease 0s;
    cursor: pointer;
}

.button-group__button_prev:hover,
.button-group__button_prev:focus-within,
.button-group__button_next:hover,
.button-group__button_next:focus-within {
    background-color: #ebebeb;
}

.button-group__button_prev.swiper-button-disabled {
    opacity: 0.5;
}

.button-group__button_next.swiper-button-disabled {
    opacity: 0.5;
}

.button-group__border {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
}

