    position: absolute;
    left: 6px;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    background-color: #ffffff;
    transition: left 0.5s ease 0s, transform 0.5s ease 0s;
}

@media only screen and (min-width: 768px) {
